.btn {
   padding: 5px 10px;
   color: rgb(0, 71, 1);
   height: 34px;
   background-color: rgb(222, 255, 223);
   border: 1px solid rgb(0, 71, 1);
   cursor: pointer;
   margin-left: 12px;
   border-radius: 6px;
   transition: all 0.3s;
   &:hover {
      background-color: rgb(0, 218, 4);
   }
   @media (max-width: 479.98px) {
      margin-left: 10px;
   }
}
